.xiaohongshu-package-container {
    font-family: "SweetHeart", sans-serif;
    display: flex;
    flex-direction: column;

    .title {
        text-align: center;
        padding: 0.5rem 0;
        font-size: 4rem;
        width: 100%;
        margin-bottom: 2rem;

        span {
            color: #ff2e4d;
        }
    }

    .package-wrapper {
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;
        gap: 3rem;

        .package-card-front {
            background: #FFFFFF;
            width: 100%;
            height: 100%;
            display: flex;

            .package-title {
                font-size: 2rem;
                color: #ff2e4d;
                text-align: center;
                margin: auto;
            }
        }

        .package-card-front-2 {
            background-color: #000000;
            width: 100%;
            height: 100%;
            display: flex;
            flex-direction: column;

            .header {
                height: 3rem;
                background-color: #FFFFFF;
                display: flex;
                padding: 0 1rem;
                justify-content: space-between;

                .left {
                    display: flex;
                    .icon {
                        margin: auto 0;
                        aspect-ratio: 1;
                        height: 1.8rem;
                        border-radius: 50%;
                        background-size: cover;
                    }
    
                    .name {
                        font-family: Arial, Helvetica, sans-serif;
                        margin: auto 0 auto 0.5rem;
                        color: #000000;
                        font-size: 0.7rem;
                    }
                }

                .right {
                    display: flex;
                    .followed {
                        margin: auto 0;
                        font-family: Arial, Helvetica, sans-serif;
                        color: #ff2e4d;
                        font-size: 0.6rem;
                        padding: 0.3rem 0.5rem;
                        border-radius: 1rem;
                        border: 1px solid #ff2e4d;
                        font-weight: 700;
                    }
                }
            }

            .content {
                height: 15rem;
                display: flex;

                .content-title {
                    text-align: center;
                    margin: auto;
                    font-size: 2rem;
                    color: var(--text-hover-color);
                    font-family: Carat;
                }
            }

            .footer {
                height: 10rem;
                background-color: #FFFFFF;
                display: flex;
                flex-direction: column;

                .dots {
                    margin: 1rem auto;
                    display: grid;
                    grid-template-columns: 1fr 1fr 1fr;
                    gap: 0.15rem;

                    .dot {
                        height: 0.3rem;
                        width: 0.3rem;
                        border-radius: 50%;
                        background: #b2b2b2;

                        &.active {
                            background: #ff2e4d;
                        }
                    }
                }
            }
        }

        .package-card-back {
            width: 100%;
            height: 100%;
            display: flex;

            .benefits-container {
                margin: auto;
                font-size: 1.5rem;
                text-align: left;
            }
        }
    }
}

